html,
body {
  height: 100%;
}
body {
  font-family: "Visby CF", sans-serif;
  overflow-x: hidden;
}
.main-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.main {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
}
.container {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}
h1,
.h1 {
  font-size: 80px;
  font-weight: 900;
  line-height: 86px;
}
@media only screen and (min-width: 1025px) and (max-width: 1365px) {
  h1,
  .h1 {
    font-size: 65px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  h1,
  .h1 {
    font-size: 60px;
  }
}
@media only screen and (max-width: 767px) {
