  .h1 {
    font-size: 38px;
    line-height: 50px;
  }
}
h2,
.h2 {
  font-size: 40px;
  line-height: 42px;
  font-weight: 900;
  margin-bottom: 17px;
  text-transform: uppercase;
}
@media only screen and (max-width: 767px) {
  h2,
  .h2 {
    font-size: 29px;
    line-height: 32px;
  }
}
h3,
.h3 {
  font-size: 25px;
  font-weight: 400;
  margin-bottom: 35px;
}
@media only screen and (max-width: 767px) {
  h3,
  .h3 {
    font-size: 22px;
  }
}
h4,
.h4 {
  font-size: 18px;
  font-weight: 900;
}
.subtitle {
  line-height: 28px;
  font-size: 18px;
  color: #808080;
}
.body-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 11;
  background: rgba(0, 0, 0, 0.3);
}
.input {
  border: 1px solid #323232;
  border-radius: 7px;
  height: 48px;
  padding: 0 10px;
}
@media only screen and (max-width: 767px) {
  .input {
    height: 40px;
  }
}
.btn {
  background: #FFC125;
  box-shadow: 0px 7px 12px rgba(0, 0, 0, 0.11);
  border-radius: 7px;
  padding: 0 30px;
  min-width: 210px;
  min-height: 64px;
  text-transform: uppercase;
  font-weight: 900;
  transition: background .2s;
}
@media only screen and (max-width: 767px) {
  .btn {
    min-width: 150px;
    min-height: 46px;
  }
}
.btn:hover {
  background: #f1ac00;
}
.btn.btn-gray {
  background: #323232;
  color: #fff;
}
.btn.btn-gray:hover {
  background: #181818;
}
.bg-gray {
  background: #F8F8F8;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .desktop {
    display: none;
  }
}
.mobile {
  display: none;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .mobile {
    display: block;
  }
