/*------------ TOP SECTION -------------*/
.top-section {
  padding: 141px 0 150px 0;
  position: relative;
}
@media only screen and (min-width: 1025px) and (max-width: 1365px) {
  .top-section {
    padding: 85px 0;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .top-section {
    padding: 30px 0;
  }
}
.top-section__content {
  width: 77%;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .top-section__content {
    width: 100%;
  }
}
.top-section__banner {
  position: absolute;
  top: -16%;
  left: 31%;
  width: 88%;
  transform: translateX(100%);
  opacity: 0;
  transition: transform .8s, opacity .8s;
}
.top-section__banner.visible {
  transform: translateX(0);
  opacity: 1;
}
@media only screen and (min-width: 1025px) and (max-width: 1365px) {
  .top-section__banner {
    top: 6%;
    left: 38%;
    width: 69%;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .top-section__banner {
    display: none;
  }
}
.top-section__banner img {
  width: 100%;
}
.top-section .container {
  position: relative;
  z-index: 1;
}
.top-section h1,
.top-section .h1 {
  margin-bottom: 14px;
}
.top-section .subtitle {
  padding-right: 22%;
  margin-bottom: 57px;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .top-section .subtitle {
    padding-right: 0;
  }
}
.top-section input {
  width: 449px;
}
@media only screen and (max-width: 767px) {
  .top-section input {
    width: 100%;
  }
}
.form__search-station {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.form__search-station label {
  font-size: 18px;
  margin-bottom: 19px;
}
.form__search-station input {
  margin-bottom: 60px;
}
.bg-slider {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 74%;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .bg-slider {
    width: 100%;
  }
}
.bg-slider .slick-list {
  height: 100%;
}
.bg-slider .slick-dots {
  display: flex;
  position: absolute;
  left: 101%;
  top: 93%;
}
.bg-slider .slick-dots li {
  width: 58px;
  height: 1px;
  background: #000;
  margin-right: 19px;
}
.bg-slider .slick-dots li:last-child {
  margin-right: 0;
}
.bg-slider .slick-dots li.slick-active {
  background: #FFC125;
}
.bg-slider .slick-dots li button {
  display: none;
}
